:root {
    font-family: Arial, Helvetica, sans-serif;
    --white-grey: #dbe1e5;
    --dark-grey: #3c4148;
    --dark-grey-accent-1: #555a60;
    --dark-grey-accent-2: #4b525a;
    --blue: #3e5781;
    --dark-blue: #25395c;
    background-color: var(--white-grey);
}

html {
  scroll-behavior: smooth;
}

button {
    padding: 0;
    border: none;
}

button:hover {
    padding: 0;
    border: none;
}

#sidebar {
    width: 250px;
    height: 100%;
    background-color: var(--dark-grey);
    display: block;
    position: fixed;
    top: 0;
    padding: 15px;
    color: var(--white-grey);
    overflow-y: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

#sidebar::-webkit-scrollbar {
  display: none;
}

#sidebar ul {
    padding: 0;
    padding-bottom: 30px;
}

#nav li {
    background-color: var(--dark-grey-accent-1);
    margin: 10px 0;
    padding: 12px 10px;
    border: 2px solid var(--dark-grey-accent-1);
    border-radius: 5px;
    list-style-type: none;
}

#nav li:hover {
    cursor: pointer;
    background-color: var(--dark-grey-accent-2);
    border-color: var(--dark-grey-accent-2);
}

#sidebar a {
    text-decoration: none;
    color: var(--white-grey);
}

#sidebar p {
    text-align: left;
}

#filters-container {
    margin-bottom: 30px;
}

#filters-container > p {
    margin-bottom: 3px;
}

#filter-type {
    height: 30px;
}

#filter-character {
    height: 60px;
}

#filter-au {
    height: 30px;
}

button.filter-tag {
    margin: 3px;
    padding: 7px;
    border-radius: 5px;
    background-color: var(--dark-grey-accent-1);
    list-style-type: none;
    float: left;
    color: var(--white-grey);
}

button.filter-tag:hover {
    cursor: pointer;
    background-color: var(--dark-grey-accent-2);
}

button.filter-active {
    background-color: var(--blue);
}

button.filter-active:hover {
    background-color: var(--dark-blue);
}



#gallery {
    width: calc(100vw - 330px);
    margin-left: 290px;
    padding: 10px;
}

#gallery h1 {
    padding-top: 20px;
}

.gallery-section {
    max-width: 1400px;
}

.gallery-img-container {
    background-color: rgba(0,0,0,0);
    width: 300px;
    margin-bottom: 8px;
}

.gallery-img-container:hover {
    cursor: pointer;
    opacity: 0.75;
}

.gallery-img {
    width: 100%;
    padding: 0;
    margin: 0;
}

#clicked-image-container {
    top: 0;
    min-width: 100vw;
    min-height: 100vh;
    margin: 0;
    display: flex;
    place-items: center;
    justify-content: center;
    position: fixed;
}

#clicked-image-bg {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.6);
    position: fixed;
    z-index: 9;
}

.image-preview {
    max-width: 80vw;
    max-height: 80vh;
    z-index: 10;
    display: block;
    position: fixed;
}

#comic-preview-container {
    width: 140vw;
    height: 0;
    z-index: 10;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    position: fixed;
    place-content: center;
    justify-items: center;
    margin: auto;
}

.comic-page-preview-container {
    cursor: pointer;
    background: none;
}

.comic-page-preview-container img {
    max-width: 30vw;
    max-height: 80vh;
}

